	module.exports = {"data":{"challengeNode":{"title":"Ditch Custom CSS for Bootstrap","description":["现在我们可以清理一下之前代码，用 Bootstrap 的内置样式来替换我们之前定义的样式，这样会让我们的 Cat Photo App 看起来更简洁些。","别担心————以后我们会有大把时间来自定义我们的 CSS 样式的。","删除 <code>style</code> 元素里的 <code>.red-text</code>, <code>p</code>, 和 <code>.smaller-image</code> CSS 声明，使 <code>style</code> 元素留下的声明只有 <code>h2</code> 和 <code>thick-green-border</code>.","删除包含死链接的 <code>p</code> 元素。 然后将 <code>h2</code> 的 <code>red-text</code> class 替换为 Bootstrap 的 <code>text-primary</code> class.","最后, 将你第一个 <code>img</code> 元素的 \"smaller-image\" class 替换为 <code>img-responsive</code> class."],"challengeType":0,"fields":{"slug":"/front-end-libraries/bootstrap/ditch-custom-css-for-bootstrap","blockName":"Bootstrap","tests":[{"text":"h2 元素的 class 不应为 <code>red-text</code>。","testString":"assert(!$(\"h2\").hasClass(\"red-text\"), 'h2 元素的 class 不应为 <code>red-text</code>。');"},{"text":"h2 元素的 class 应为 <code>text-primary</code>。","testString":"assert($(\"h2\").hasClass(\"text-primary\"), 'h2 元素的 class 应为 <code>text-primary</code>。');"},{"text":"你的段落元素（p）应该不再使用 <code>Monospace</code> 字体。","testString":"assert(!$(\"p\").css(\"font-family\").match(/monospace/i), '你的段落元素（p）应该不再使用 <code>Monospace</code> 字体。');"},{"text":"移除你第一张图片的 class 属性 <code>smaller-image</code>。","testString":"assert(!$(\"img\").hasClass(\"smaller-image\"), '移除你第一张图片的 class 属性 <code>smaller-image</code>。');"},{"text":"给你的第一张图片添加 class 属性 <code>img-responsive</code>。","testString":"assert($(\".img-responsive\").length > 1, '给你的第一张图片添加 class 属性 <code>img-responsive</code>。');"}]},"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<link href=\"https://fonts.googleapis.com/css?family=Lobster\" rel=\"stylesheet\" type=\"text/css\">\n<style>\n  .red-text {\n    color: red;\n  }\n\n  h2 {\n    font-family: Lobster, Monospace;\n  }\n\n  p {\n    font-size: 16px;\n    font-family: Monospace;\n  }\n\n  .thick-green-border {\n    border-color: green;\n    border-width: 10px;\n    border-style: solid;\n    border-radius: 50%;\n  }\n\n  .smaller-image {\n    width: 100px;\n  }\n</style>\n\n<div class=\"container-fluid\">\n  <h2 class=\"red-text text-center\">CatPhotoApp</h2>\n\n  <p>Click here for <a href=\"#\">cat photos</a>.</p>\n\n  <a href=\"#\"><img class=\"smaller-image thick-green-border\" src=\"https://bit.ly/fcc-relaxing-cat\" alt=\"A cute orange cat lying on its back.\"></a>\n\n  <img src=\"https://bit.ly/fcc-running-cats\" class=\"img-responsive\" alt=\"Three kittens running towards the camera.\">\n  <div class=\"row\">\n    <div class=\"col-xs-4\">\n      <button class=\"btn btn-block btn-primary\">Like</button>\n    </div>\n    <div class=\"col-xs-4\">\n      <button class=\"btn btn-block btn-info\">Info</button>\n    </div>\n    <div class=\"col-xs-4\">\n      <button class=\"btn btn-block btn-danger\">Delete</button>\n    </div>\n  </div>\n  <p>Things cats love:</p>\n  <ul>\n    <li>cat nip</li>\n    <li>laser pointers</li>\n    <li>lasagna</li>\n  </ul>\n  <p>Top 3 things cats hate:</p>\n  <ol>\n    <li>flea treatment</li>\n    <li>thunder</li>\n    <li>other cats</li>\n  </ol>\n  <form action=\"/submit-cat-photo\">\n    <label><input type=\"radio\" name=\"indoor-outdoor\"> Indoor</label>\n    <label><input type=\"radio\" name=\"indoor-outdoor\"> Outdoor</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Loving</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Lazy</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Crazy</label>\n    <input type=\"text\" placeholder=\"cat photo URL\" required>\n    <button type=\"submit\">Submit</button>\n  </form>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"nextChallengePath":"/front-end-libraries/bootstrap/use-a-span-to-target-inline-elements","id":"bad87fee1347bd9aedf08845"},"slug":"/front-end-libraries/bootstrap/ditch-custom-css-for-bootstrap"}}

